import { NavController, NavParams } from 'ionic-angular';
import { Component } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms'

@Component({
  selector: 'page-formulario',
  templateUrl: 'formulario.html',
})
export class Formulario {

  visita : any ={};

  constructor(public navCtrl: NavController, public formBuilder: FormBuilder, public navParams: NavParams) {
    this.visita = this.formBuilder.group({
      endereco:['',Validators.required],
      numero:['',Validators.required],
      complemento:['',Validators.required],
      isFoco:['',Validators.required],
      descricao:['',Validators.required]

    });
  }

  public postForm(){
    console.log("Vem do Form: "+this.visita.value);
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad Formulario');
  }

;

}



